import * as React from 'react';
import 'react-native-gesture-handler';
import { createStackNavigator } from '@react-navigation/stack';

import ManageOrdersScreen from './ManageOrdersScreen';
import ManageProductScreen from './ManageProductScreen';
import RegisterScreen from './RegisterScreen';
import UserDetailsScreen from './UserDetailsScreen';
import MenuScreen from './MenuScreen';

const Stack = createStackNavigator();

function HomeScreen() {
    return (
        <Stack.Navigator initialRouteName="Menu">
            <Stack.Screen
                name="Menu"
                component={MenuScreen}
                options={{ title: 'Menu' }}
            />
            <Stack.Screen
                name="Manage User"
                component={UserDetailsScreen}
                options={{ title: 'Manage User' }}
            />
            <Stack.Screen
                name="Manage Product"
                component={ManageProductScreen}
                options={{ title: 'Manage Product' }}
            />
            <Stack.Screen
                name="Manage Orders"
                component={ManageOrdersScreen}
                options={{ title: 'Manage Orders' }}
            />
            <Stack.Screen
                name="Register User"
                component={RegisterScreen}
                options={{ title: 'Register User' }}
            />
        </Stack.Navigator>
    );
}

export default HomeScreen;
