import React from 'react';
import { View, Text, StyleSheet, TouchableOpacity } from 'react-native';

const MenuScreen = ({ navigation }) => {
    const screens = [
        { name: 'Manage Product', route: 'Manage Product' },
        { name: 'Manage Orders', route: 'Manage Orders' },
        { name: 'Manage User', route: 'Manage User' },
        { name: 'Register User', route: 'Register User' },
    ];

    return (
        <View style={styles.container}>
            {screens.map((screen, index) => (
                <TouchableOpacity
                    key={index}
                    style={styles.card}
                    onPress={() => navigation.navigate(screen.route)}
                >
                    <Text style={styles.text}>{screen.name}</Text>
                </TouchableOpacity>
            ))}
        </View>
    );
};

const styles = StyleSheet.create({
    container: {
        flex: 1,
        alignItems: 'center',
        justifyContent: 'center',
    },
    card: {
        backgroundColor: '#e0e0e0',
        padding: 20,
        marginVertical: 10,
        width: '80%',
        alignItems: 'center',
    },
    text: {
        fontSize: 18,
    },
});

export default MenuScreen;
